<style lang="less">
@import "./css/common.less";
#container {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.child-view {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}
</style>
<template>
  <div id="container">
    <transition :name="transitionName">
      <router-view id="app" class="child-view"></router-view>
    </transition>
  </div>
</template>
<script>
export default {
  name: "app",
  data() {
    return {
      tempLength: 0
    };
  },
  computed: {
    //计算当前路由
    transitionName() {
      return this.$store.state.routerRecord.transitionName;
    }
  },
  mounted() {
    //禁止所有元素的回弹
    // window.addEventListener("touchmove", e => {
    //   e.preventDefault();
    //   e.stopPropagation();
    // });
    // document.querySelector("#container").addEventListener("touchmove", e => {
    //   e.preventDefault();
    //   e.stopPropagation();
    // });

    //禁止缩放
    document.documentElement.addEventListener(
      "touchstart",
      event => {
        if (event.touches.length > 1) {
          event.preventDefault();
        }
      },
      false
    );
    //禁用手指双缩放
    var lastTouchEnd = 0;
    document.documentElement.addEventListener(
      "touchend",
      function(event) {
        var now = Date.now();
        if (now - lastTouchEnd <= 300) {
          event.preventDefault();
        }
        lastTouchEnd = now;
      },
      false
    );
  }
};
</script>




