import Vue from 'vue'
import vueRouter from 'vue-router'
Vue.use(vueRouter)

const ACCOUNTMG = () => import('../pages/AccountMg.vue')
const ASSESS = () => import('../pages/assess.vue')
const FORGET = () => import('../pages/forget.vue')
const HOME = () => import('../pages/home.vue')
const LOGIN = () => import('../pages/login.vue')
const MYCLAIM = () => import('../pages/myClaim.vue')
const MYORDER = () => import('../pages/myOrder.vue')
const MYPOLICY = () => import('../pages/myPolicy.vue')
const PERSONAL = () => import('../pages/personal.vue')
const RESETPWD = () => import('../pages/resetPwd.vue')
const SETTINGS = () => import('../pages/settings.vue')

var router = new vueRouter(
{
 routes: 
 [
{
 path: '/', 
 redirect: '/login'}, 
{
 path: '/AccountMg', 
 component: ACCOUNTMG}, 
{
 path: '/assess', 
 component: ASSESS}, 
{
 path: '/forget', 
 component: FORGET}, 
{
 path: '/home', 
 component: HOME}, 
{
 path: '/login', 
 component: LOGIN}, 
{
 path: '/myClaim', 
 component: MYCLAIM}, 
{
 path: '/myOrder', 
 component: MYORDER}, 
{
 path: '/myPolicy', 
 component: MYPOLICY}, 
{
 path: '/personal', 
 component: PERSONAL}, 
{
 path: '/resetPwd', 
 component: RESETPWD}, 
{
 path: '/settings', 
 component: SETTINGS}
] 
}
)

export default router


