// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import * as filters from './filters'

//注册全局过滤器
Object.keys(filters).forEach(key => {
    Vue.filter(key, filters[key])
})

//路由守卫
router.beforeEach((to, from, next) => {
    next()
})

//引入mint-ui
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.use(MintUI)

//个人一些组件相关
import './libs/vueExtend.js' //vue自定义指令相关

//筋斗云前端框架相关
//js部分已经在首页引入
import './libs/weui.min.css'
import './libs/mui.css'



//动态计算rem
import './libs/flexible'

//引入全局数据请求方式
import httpServer from "./common/httpServer";
Vue.prototype.$http = new httpServer();

//引入自定义的校验方式
import valid from './libs/validate'
Vue.prototype.$valid = valid


//书写全局back方法
Vue.prototype.$back = function() {
    store.dispatch('setTransitionName', 'slide-right').then(() => {
        window.history.back(-1);
    })

};
//全局调转方法
Vue.prototype.$link = function(hash) {
    store.dispatch('setTransitionName', 'slide-left').then(() => {
        router.push(hash)
    })
};
//扩展vue方法 调用echarts功能
Vue.prototype.$echarts = echarts

document.addEventListener('deviceready', function() {
    /* eslint-disable no-new */
    new Vue({
        el: '#app',
        router,
        store,
        render: create => create(App),
    })
    window.navigator.splashscreen.hide()
}, false);


