import { Toast } from 'mint-ui';
//表单校验 参数是一个数组  
var valid = valid || {};
/**
 * params= [ {data: '待验证数据',msg:'失败消息', callback: '失败回调',required: true,
 *           validate: {Reg: //, msg: '', callback: fn} }]
 *validate 自定义校验规则 目前只支持正则 包括失败的内容 和失败的回调
 *
 */
/**
 * @param params 验证的数据 格式如上
 * @returns {*} 成功返回true 失败返回false
 */
valid.validate = function(params) {
        var flag = true;
        if (params !== undefined && params instanceof Array) {
            for (var i = 0; i < params.length; i++) {
                for (var key in params[i]) {
                    if (params[i].required) {
                        if (params[i].data === undefined || params[i].data === '') {
                            //验证失败时候的回调 有回调执行回调 没有 不执行
                            if (params[i].callback && typeof params[i].callback === 'function') {
                                Toast({
                                    message: params[i].msg,
                                    position: 'center',
                                    duration: 2000
                                });
                            } else {
                                Toast({
                                    message: params[i].msg,
                                    position: 'center',
                                    duration: 2000
                                });
                            }
                            flag = false;
                            return flag;
                        }
                    }
                    // validate 提供正则校验 格式 validate: {Reg: //, msg: '', callback: fn}
                    if (params[i].validate !== undefined && params[i].validate.Reg !== undefined && typeof params[i].validate.Reg === 'object') {
                        if (!params[i].validate.Reg.test(params[i].data)) {
                            //验证失败时候的回调 
                            if (params[i].validate.callback && typeof params[i].validate.callback === 'function') {
                                Toast({
                                    message: params[i].validate.msg,
                                    position: 'center',
                                    duration: 2000
                                });
                            } else {
                                Toast({
                                    message: params[i].validate.msg,
                                    position: 'center',
                                    duration: 2000
                                });
                            }
                            flag = false;
                            return flag;
                        }
                    }
                    //自定义函数 validator: {hander: fn, msg: '', callback: fn}
                    if (params[i].validator !== undefined && typeof params[i].validator.hander === 'function') {
                        //执行函数
                        if (!params[i].validator.hander()) {
                            if (params[i].validator.callback && typeof params[i].validator.callback === 'function') {
                                Toast({
                                    message: params[i].validator.msg,
                                    position: 'center',
                                    duration: 2000
                                });
                            } else {
                                Toast({
                                    message: params[i].validator.msg,
                                    position: 'center',
                                    duration: 2000
                                });
                            }
                            flag = false;
                            return flag;
                        }
                    }
                }
                if (!flag) {
                    break
                }
            }
        } else {
            throw new Error('验证值请传入一个数组')
            return false;
        }
        return flag;
    }
    //常用验证规则
valid.ruls = {
    /**
     * @param data 验证的数据
     * @param callback 回调函数
     * @returns {*} 校验规则
     */
    phone: function(data, callback) {
        return {
            data: data,
            required: true,
            msg: '请输入手机号',
            callback: callback,
            validate: {
                Reg: /^1[34578]\d{9}$/,
                msg: "请输入正确的手机号",
                callback: callback
            }
        }
    },
    /**
     * @param data 验证的数据
     * @param callback 回调函数
     * @returns {*} 校验规则
     */
    code: function(data, callback) {
        return {
            data: data,
            required: true,
            msg: '请输入验证码',
            callback: callback,
            validate: {
                Reg: /^\d{4}$/,
                msg: "验证码格式错误",
                callback: callback
            }
        }
    },

    /**
     * @param data 验证的数据
     * @param callback 回调函数
     * @returns {*} 校验规则
     */
    verifyCode: function(data, callback) {
        return {
            data: data,
            required: true,
            msg: '请输入图形验证码',
            callback: callback,
            validate: {
                Reg: /^[0-9A-Za-z]{4}$/,
                msg: "图形验证码错误",
                callback: callback
            }
        }
    },



    /**
     * @param data 验证的数据
     * @param callback 回调函数
     * @returns {*} 校验规则
     */
    account: function(data, callback) {
        return {
            data: data,
            required: true,
            msg: "请输入用户名",
            callback: callback,
            validate: {
                Reg: /^[0-9A-Za-z]{5,16}$/,
                msg: "用户名格式错误",
                callback: callback
            }
        }
    },
    /**
     * @param data 验证的数据
     * @param callback 回调函数
     * @returns {*} 校验规则
     */
    password: function(data, callback) {
        return {
            data: data,
            required: true,
            msg: "请输入密码",
            callback: callback,
            validate: {
                Reg: /^[0-9A-Za-z]{5,16}$/,
                msg: "密码格式错误",
                callback: callback
            }
        }
    },
    /**
     * @param data 验证的数据
     * @param callback 回调函数
     * @returns {*} 校验规则
     */
    cardId: function(data, callback) {
        return {
            data: data,
            required: true,
            msg: '请输入身份证号码',
            callback: callback,
            validate: {
                Reg: /^[1-9]\d{5}[1-9]\d{3}((0[1-9])|(1[0-2]))((0[1-9])|([1-2][0-9])|(3[0-1]))\d{3}(\d|x|X)$/,
                msg: '请输入正确的身份证号',
                callback: callback
            }
        }
    }
}
export default valid


