// 数据请求地址 通过全局变量 打包引用不同的地址
import axios from 'axios'
import { Toast, Indicator } from 'mint-ui';
export default class HttpServer {
    constructor() {
        this.baseUrl = location.protocol + (process.env.NODE_ENV === 'development' ? '//sso.test.anyi-tech.com' : '//sso.anyi-tech.com'); //根据环境 配置不同域名
        this.url = {
            "login": this.baseUrl + "/ws_sso/ssoUser/login",
            "logout": this.baseUrl + "/ws_sso/ssoUser/logout",
            "loginByPhone": this.baseUrl + "/ws_sso/ssoUser/loginByPhone",
            "wechatLogin": this.baseUrl + "/ws_sso/weixin/phoneBindWeixin",
            "getUserInfoByUserId": this.baseUrl + "/ws_sso/ssoUser/getUserInfoByUserId",
            "getCaptcha": this.baseUrl + "/ws_sso/captcha/getCaptcha?width=80&height=40",
            "regist": this.baseUrl + "/ws_sso/ssoUser/regist",
            "getCode": this.baseUrl + "/ws_sso/phone/getCode",
            "checkCode": this.baseUrl + "/ws_sso/phone/checkCode",
            "setCookie": this.baseUrl + "/ws_sso/auth/setCookie",
            "resetPwd": this.baseUrl + "/ws_sso/ssoUser/resetPassword"
        }
        this.captche = ''; //图形验证码匹配字符 
        this.version = 0;
        this.sso_token = window.localStorage.sso_token;
        this.cookie = window.localStorage.cookie;

    };

    /** 
     * @param {*} params 
     */
    //账号密码登录
    login(params) {
        params = params || {}
        params.url = this.url.login;
        return new Promise((resolve, reject) => {
            this.post(params).then((res) => {
                //登录成功 设置cookie
                this.setCookie("sso-token", res.result.token, 1 / 48);
                resolve(res)
            }, (err) => {
                reject(err);
            })
        })
    };

    //手机动态登录
    loginByPhone(params) {
        params = params || {};
        params.url = this.url.loginByPhone;
        return new Promise((resolve, reject) => {
            this.post(params).then((res) => {
                //登录成功 设置cookie
                this.setCookie("sso-token", res.result.token, 1 / 48);
                resolve(res)
            }, (err) => {
                reject(err);
            })
        })
    }

    /** 
     * @param {*} params 
     */
    //获取用户信息
    getUserInfo(params) {
        params = params || {};
        params.url = this.url.getUserInfoByUserId;
        return new Promise((resolve, reject) => {
            this.post(params).then((res) => {
                resolve(res);
            }, (err) => {
                reject(err);
            })
        })
    };

    /** 
     * @param {*} params 
     */
    //获取图形验证码
    getCaptcha(params) {
        params = params || {};
        params.url = this.url.getCaptcha;
        return new Promise((resolve, reject) => {
            this.get(params).then((res) => {
                this.captche = res.result.captche
                resolve(res);
            }, (err) => {
                reject(err);
            })
        })
    };

    /** 
     * @param {*} params 
     */
    //获取手机验证码
    getCode(params) {
        params = params || {};
        params.url = this.url.getCode;
        return new Promise((resolve, reject) => {
            this.post(params).then((res) => {
                resolve(res);
            }, (err) => {
                reject(err);
            })
        })
    };

    /**
     * @param {*} params 
     */
    //验证手机号
    checkCode(params) {
        params = params || {};
        params.url = this.url.checkCode;
        return new Promise((resolve, reject) => {
            this.post(params).then((res) => {
                resolve(res);
            }, (err) => {
                reject(err);
            })
        })
    }

    /**
     * @param {*} params
     */
    //重置密码
    resetPwd(params) {
        params = params || {};
        params.url = this.url.resetPwd;
        return new Promise((resolve, reject) => {
            this.post(params).then((res) => {
                resolve(res);
            }, (err) => {
                reject(err);
            })
        })
    }

    /**
     * 获取公共post请求 
     * @param {method} 请求方式
     * @param {url} 请求地址
     * @param {data} 请求数据
     * @param {loading} 是否执行加载图标
     * @returns {Promise} 异步对象
     */
    post(params) {
        var config = {
            method: 'post',
            url: params.url,
            data: this.filterObjNull(params.data),
            headers: {
                "sso-token": this.getCookie("sso-token") || '',
                "captche": this.captche || ''
            }
        };

        return new Promise((resolve, reject) => {
            //开启数据加载图标
            if (params.loading) {
                Indicator.open({
                    text: '加载中...',
                    spinnerType: 'snake'
                });
            };
            axios(config).then((res) => {
                //关闭加载动画
                if (params.loading) {
                    Indicator.close();
                };
                if (res.status === 200 || res.status === 304) {
                    //请求成功
                    if (res.data.code === -1 || res.data.code === 0) {
                        Toast({
                            message: res.data.msg,
                            position: 'center',
                            duration: 2000
                        });
                        reject(res.data);
                    } else {
                        resolve(res.data);
                    }
                } else if (res.status >= 400) {
                    Toast({
                        message: '服务器错误',
                        position: 'center',
                        duration: 2000
                    });
                    reject(res);
                }
            }).catch((err) => {
                //关闭加载动画
                if (params.loading) {
                    Indicator.close();
                };
                reject(err);
                //401 没有sso-token 跳转到登录页
                if (err.response.status === 401) {
                    //跳转时 后面可拼接当前地址登录完成后跳转回来
                    window.location.href = '/#/login'
                }
            });
        });
    };

    /**
     * 获取公共get 请求
     * @param {url} 请求地址
     * @param {loading} 是否执行加载图标
     * @returns {Promise} 异步对象
     */
    get(params) {
        return new Promise((resolve, reject) => {
            //开启数据加载图标
            if (params.loading) {
                Indicator.open({
                    text: '加载中...',
                    spinnerType: 'snake'
                });
            };
            axios({ url: params.url, method: 'get', headers: { "sso-token": this.getCookie("sso-token") } }).then(function(response) {
                if (params.loading) {
                    Indicator.close();
                };
                resolve(response.data);
            }).catch(function(error) {
                if (params.loading) {
                    Indicator.close();
                };
                reject(error);
            });
        });
    };

    /**
     * 获取URL中参数
     * @param name 参数名称
     * @returns {*}
     */
    getUrlParam(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) return decodeURIComponent(r[2]);
        return null;
    };

    /**
     * 获取cookie
     * @param name
     * @param value
     * @param days
     * @returns {*}
     */
    getCookie(name, value, days) {
        if (arguments.length == 1) {
            var nameEQ = name + "=";
            var ca = document.cookie.split(';');
            for (var i = 0; i < ca.length; i++) {
                var c = ca[i];
                while (c.charAt(0) == ' ') c = c.substring(1, c.length);
                if (c.indexOf(nameEQ) == 0) return decodeURIComponent(c.substring(nameEQ.length, c.length));
            }
            return null;
        } else if (arguments.length > 1) {
            setCookie(name, encodeURIComponent(value), days);
        }
    };

    /**
     * 设置cookie
     * @param {string} name  键名
     * @param {string} value 键值
     * @param {integer} days cookie周期
     */
    setCookie(name, value, days) {
        if (days) {
            var date = new Date();
            date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
            var expires = "; expires=" + date.toGMTString();
        } else {
            var expires = "";
        }
        document.cookie = name + "=" + value + expires + "; path=/";
    };

    /**
     * 移除cookie
     * @param name
     */
    removeCookie(name) {
        setCookie(name, "", -1);
    };

    /**
     * 过滤所有对象空字段
     * @param {*} params
     * @returns {*}
     */
    filterObjNull(params) {
        if (params && typeof params == 'object') {
            var obj = {};
            Object.keys(params).forEach((item) => {
                if (params.hasOwnProperty(item)) {
                    if (params[item] || params[item] === 0 || params[item] === false) obj[item] = params[item];
                }
            });
            return obj;
        } else {
            return params;
        }
    }
}


