//记录全局路由跳转记录
const state = {
    transitionName: 'slide-left', //路由过渡动画名称
}

//计算状态 用于过滤等操作
const getters = {
    transitionName: transitionName => state.transitionName,
}

//动作 actions 执行异步操作  
//我们可以将一些数据请求 放在这里 页面执行this.$store.dispatch('actionName', param)
const actions = {
    setTransitionName({ commit, state }, param) {
        return new Promise((resolve, reject) => {
            commit('setTransitionName', param)
            resolve(param)
        })
    }
}

//执行 执行同步操作
const mutations = {
    setTransitionName(state, params) {
        state.transitionName = params
    }
}

//暴露成员 实现模块化
export default {
    state,
    getters,
    actions,
    mutations
}


